import ProductCard from '../../components/ProductCard';
import RelatedProducts from '../../components/RelatedProducts';

async function getProduct(id) {
  try {
    const res = await fetch(`${process.env.NEXT_PUBLIC_BASE_URL}/api/products/${id}`, {
      next: { revalidate: 60 }, // optional for ISR
    });

    if (!res.ok) {
      throw new Error(`Product fetch failed: ${res.status}`);
    }

    return res.json();
  } catch (error) {
    console.error('getProduct error:', error);
    return null;
  }
}

async function getProducts() {
  try {
    const res = await fetch(`${process.env.NEXT_PUBLIC_BASE_URL}/api/products`, {
      next: { revalidate: 60 }, // optional
    });

    if (!res.ok) {
      throw new Error(`Products fetch failed: ${res.status}`);
    }

    return res.json();
  } catch (error) {
    console.error('getProducts error:', error);
    return [];
  }
}

export default async function ProductDetailPage({ params }) {
  const product = await getProduct(params.id);
  const products = await getProducts();

  if (!product) {
    return (
      <div className="container mx-auto px-4 py-8 text-center">
        <h1 className="text-2xl font-bold text-red-500">محصول یافت نشد</h1>
        <Link href="/products" className="text-blue-600 hover:underline mt-4 inline-block">
          بازگشت به صفحه محصولات
        </Link>
      </div>
    );
  
  }

  return (
    <div className="container mx-auto px-4 py-8">
      {/* Product Card */}
      <div className="max-w-md mx-auto mb-12">
        <ProductCard product={product} />
      </div>

      {/* Product Details */}
      <div className="bg-white p-6 rounded-lg shadow-md mb-12">
        <h2 className="text-xl font-bold mb-4">توضیحات محصول</h2>
        <p className="text-gray-700">{product.description || 'توضیحاتی برای این محصول موجود نیست.'}</p>
      </div>

      {/* Related Products */}
      <RelatedProducts products={products} currentProductId={product.id} />
    </div>
  );
}
